.footer {
    width: 100%;
    height: 100%;
    margin-top: -1px;
    color: #FFF;
    background-color: #161616;
    font-family: "Monserrat", sans-serif;
}

.footerSpacer {
    height: 230px;
}

.footerLogo {
    width: 80%;
    height: 100%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footerBtn {
    border: 1px solid #46C0EA;
    color: #FFF;
    margin: 0px 10px;
    padding: 10px 20px;
    font-size: 1.1rem;
    font-weight: 400;
    letter-spacing: 2px;
    border-radius: 3px;
    text-decoration: none;
    text-transform: uppercase;
    transition: 0.2s ease;
}

.footerBtn:hover {
    background-color: #46C0EA;
}

.footerLinksSection {
    width: 80%;
    height: 100%;
    margin: 50px auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #B3B3B3;
    letter-spacing: 1.5px;
}

.footerLinks {
    display: block;
    margin: 5px auto;
    text-decoration: none;
    color: #B3B3B3;
    font-size: 0.9rem;

}

.footerCopy {
    height: 70px;
    border-top: 1px solid #2c2c2c;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #B3B3B3;
}

.footerCopyH6 {
    display: inline;
    font-size: 0.8rem;
    font-weight: 400;
}